.h2 {
    margin-bottom: 23px;
    color: $main-red;
    font-family: Bebas;
    font-size: 18px;
    font-weight: 400;
    line-height: 18px;
    // +css-lock(14,18,600,991)
    &_theme_gradient {
       font-size: 14px;
       position: relative;
       background: url('../img/gradient.png');
       line-height: 11px;
       @include css-lock(11,14,601,991);
       @include r(600) {
            font-size: 14px; } } // as sidebar will be 100% lower 600px of screen width, need h2 in sidebar block be the same (14px) with h2 in content block
    &_footer_white {
        color: #fff; }
    &__link {
        color: inherit;
        font-size: inherit;
        font-weight: inherit;
        line-height: inherit; }
    &__span {
        padding-right: 16px;
        background: #fff; }
    &__sublink {
        color: #a0a0a0;
        font-family: "Helvetica Neue";
        font-size: 12px;
        font-weight: 400;
        line-height: 18px; } }
