.header {
    padding: 22px 0; }
.footer {
    background: #f3b29d;
    &__wrap {
        margin: 0 -10px;
        padding: 38px 35px;
        border-left: 10px solid #fff;
        border-right: 10px solid #fff;
        box-sizing: border-box;
        background: $main-red;
        @include r(767) {
            border: 0; } }
    &__mssg {
        margin-bottom: 20px; }
    &__item {
        margin: 0 32px;
        width: 33%;
        float: left;
        @include r(500) {
            margin: 0 0 20px 0;
            width: 100%;
            float: none; } }
    &__text {
        font-family: "Helvetica Neue";
        font-size: 12px;
        line-height: 14px;
        color: #fff;
        font-weight: 400; }
    &__span {
        font-family: "Helvetica Neue";
        font-size: 12px;
        line-height: 14px;
        color: $secondary-pink;
        font-weight: 400; }
    &__time {
        margin-top: 8px;
        color: $secondary-pink;
        font-family: "Helvetica Neue";
        font-size: 12px;
        font-weight: 400;
        line-height: 14px; } }
