.sidebar {
    width: 25%;
    float: right;
    @include r(600) {
        width: 100%;
        float: none; }
    &__item {
        margin-bottom: 37px; }
    &__link {
        color: #464646;
        font-family: "Helvetica Neue";
        font-size: 12px;
        font-weight: 400;
        @include css-lock(10,12,600,991); }
    &__portfolio {
        display: grid;
        grid-template-columns: repeat(4, 20%);
        grid-gap: 5%; } }
