.menu {
    padding-top: 12px;
    float: right;
    @include r(767) {
        padding-top: 9px; }
    @include r(500) {
        padding: 0;
        max-height: 0;
        background-color: #fff;
        float: none;
        overflow: hidden;
        text-align: center;
        transition: max-height 0.7s ease;
        &_js {
            max-height: 500px;
            transition: max-height 1.5s ease; } }
    &__item {
        margin-left: 26px;
        display: inline-block;
        @include r(500) {
            margin-top: 10px;
            display: block; } }
    &__link {
        color: #b9b9b9;
        font-family: Bebas;
        font-size: 16px;
        font-weight: 400;
        text-transform: uppercase;
        &:hover {
            color: #e8603c; }
        @include css-lock(13, 16, 600, 991); } }
