* {
	margin: 0;
	padding: 0;
	outline: none; }
body {
	background: url('../img/bg.png');
	line-height: 1.4; }
a {
	text-decoration: none; }
p {
	@include css-lock(12, 16, 600, 1000); }
h2 {
	@include css-lock(14,18,600,991); }
img {
	width: 100%; }
.container {
	margin: 0 auto;
	@include r(767) {
		padding: 0 10px;
		width: 100%; }
	@include rmin(768) {
		width: 750px; }
	@include rmin(992) {
		width: 940px; } }
.clearfix {
	clear: both;
	&__sm {
		display: none;
		@include r(500) {
			display: block; } } }
